.users {
    overflow-y: auto;
    position: relative;
    .usersPageTitle {
        position: absolute;
        width: calc(100% - 1rem);
        height: 2.5rem;
        display: flex;
        flex-direction: row;
        margin-left: 1rem;
        align-items: center;
        justify-content: space-between;
        font-size: 1.5rem;
        background: #fff;
        border-bottom: 1px solid #e6e6e6;
        border-radius: 12px;
        z-index: 10; }
    .blank {
        height: 2.25rem; }
    .usersWrapper {
        height: calc(100vh - 60px - 2.25rem - .65rem);
        overflow-y: auto; } }
.btnGroupWrapper {
    display: flex;
    align-items: center; }
.wrapperStyle {
    height: 2.5rem;
    margin: 1rem auto 3rem;
    display: flex;
    align-items: flex-end; }
.buttonStyle {
    max-width: 7.5rem; }
.bottomPaginatorWrapper {
    margin: 0 1rem 0 1rem; }
@media (max-width: 767px) {
    .users {
        .usersWrapper {
            height: calc(100vh - 50px - 2.25rem - .5rem - .5rem); } } }
@media (max-width: 450px) {
    .users {
        .usersPageTitle {
            width: calc(100% - .5rem);
            margin-left: .5rem; } } }
