.header {
    min-width: 300px;
    width: 100%;
    height: 40px;
    line-height: 1;
    grid-column: 1 / span 3;
    margin: 10px 0;
    display: flex;
    align-items: center;
    background-color: #25bdd8;
    border-radius: 3px;
    .headerTitleWrapper {
        flex: 1;
        transition: all .5s;
        .headerMainTitle {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin: 0;
            padding: 0 1rem;
            text-align: center;
            font-size: 2rem;
            color: #f0f1f3;
            a {
                color: inherit;
                font: inherit;
                text-decoration: none;
                font-size: 1rem; } } } }
.userName {
    font-size: 2rem;
    color: #333;
    text-decoration: none;
    transition: all .2s;
    &:hover {
        text-decoration: underline;
        color: #47525E; } }
.wrapperStyle {}
.btnStyle {
    background: none;
    text-align: right;
    &:hover {
        background: none; } }
@media (max-width: 768px) {
    .additionalTitleContent {
        display: none; }
    .header {
        width: 100%;
        height: 50px;
        line-height: 1;
        margin: 0;
        .headerTitleWrapper {
            .headerMainTitle {} } } }
@media (min-aspect-ratio: 4/3) {
    .header {
        width: 100%; } }


