.statusForm {
    flex: 2;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
    padding-bottom: .5rem;
    .statusField {
        flex: 1;
        width: 100%;
        margin: .5rem 0;
        margin-bottom: 1rem;
        padding: .5rem;
        font-weight: 700;
        border: 1px solid #d1d1d1;
        border-radius: 5px;
        overflow: hidden; }
    .statusInput {
        flex: 1;
        width: 100%;
        margin: .5rem 0;
        margin-bottom: 1rem;
        padding: .5rem;
        background: #81818146;
        border: none;
        border-radius: 5px;
        outline: none;
        font: inherit;
        font-size: inherit;
        outline: none; } }
.wrapperStyle {
    flex: none;
    width: 100%;
    height: 2rem;
    margin: 0; }

@media (max-width: 767px) {
    .wrapper {
        margin-right: 1rem;
        .statusField {
            font-size: 1.1rem; } } }
@media (max-width: 450px) {
    .statusForm {
        flex: 2;
        padding-bottom: .5rem;
        .statusField {
            margin-bottom: .5rem;
            border-radius: 5px; }
        .statusInput {
            margin-bottom: .5rem;
            border-radius: 5px; } } }
