.create-post {
    padding: 0;
    h3 {
        margin: 0 .5rem .5rem;
        margin-bottom: 1rem; }
    .title {
        padding: .65rem; }
    .create-post__input_wrap {
        height: 3rem;
        position: relative;
        border-radius: .5rem;
        .create-post__input {
            width: 100%;
            height: 100%;
            padding-left: 1rem;
            background: #d7faff;
            outline: none;
            border: none;
            border-radius: .5rem;
            &:focus {
               background: #e8f0fe; }
            &:focus::placeholder {
                opacity: 0; }
            &:disabled {
                background: #e1f1f3; } }
        ::placeholder {
            position: absolute;
            top: 10px;
            left: 10px;
            font-size: 1rem;
            transition: opacity 0.3s; } } }

.error {
    margin: 1rem 0 0 1rem;
    color: #ff0000; }
.wrapperStyle {
    flex-direction: row;
    justify-content: flex-end;
    padding: .5rem 0 0 1rem;
    button {
        width: 12rem;
        height: auto;
        padding: .5rem 1rem;
        font-size: 1rem; } }
